<template id="">
  <div class="m-index" id="m-index">
    <router-view></router-view>
    <footer>
      <ul>
        <li @click="changeNav(i)" :class="{active: i==navIndex}" v-for="(fn,i) in footerNav">
          <router-link :to="fn.router">
            <i class="yo-ico" v-html="fn.ico"></i>
            <b>{{fn.title}}</b>
          </router-link>
        </li>
      </ul>
    </footer>
  </div>
</template>

<script>
  module.exports = {
    data: function () {
      return {
        navIndex: 0,
        footerNav: [
          {
            router: '/',
            ico: '&#xe604;',
            title: '首页'
          },
          {
            router: '/search',
            ico: '&#xe660;',
            title: '发现'
          },
          {
            router: '/photo',
            ico: '&#xe605;',
            title: ''
          },
          {
            router: '/my',
            ico: '&#xe64d;',
            title: '我的'
          },
          {
            router: '/exit',
            ico: '&#xe61b;',
            title: '退出'
          }
        ]
      }
    },
    methods: {
      changeNav: function (index) {
        this.navIndex = index;
      }
    }
  }
</script>



